@use "global" as *;

.sub-title {
    font-family: $second-font-family;
    font-weight: 500;
    font-size: 22px;
    position: relative;
    margin-left: rem(42);
    line-height: 1.5909090909;
    letter-spacing: 0.1em;
    //////
    // display: inline-flex;
    // -moz-column-gap: rem(40);
        //  column-gap: rem(40);
    // align-items: center;
        &::before {
        content: '';
        position: absolute;
        background-image: url(../images/screening/sp_icon.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: rem(32);
        height: rem(32);
        left: rem(-40);
        vertical-align: middle;
        ////
        // display: inline-block;
        @include mq('md') {
            width: 30px;
            height: 30px;
            left: -40px;
        }
        @include mq('lg') {
            width: rem(47);
            height: rem(47);
            left: rem(-62);
            font-size: 28px;
        }
    }

    @include mq('xl') {
        font-size: 30px;
        margin-left: 0;
    }
}

#screening .sub-title {
    margin-left: rem(41);
    @include mq('md') {
        margin-left: rem(-7);
    }
}

#screening .sub-title span {
    padding-left: rem(8);
    @include mq('md') {
        padding-left: 0;
    }
}

#effect .sub-title {
    &::before {
        display: none;
        @include mq('md') {
            display: block;
        }
    }

}