@use "global" as *;

.contact {
    padding-top: 36px;
    padding-bottom: 40px;
    text-align: center;
    background-color: #FAEEEC;

    @include mq('lg') {
        padding-top: 74px;
        padding-bottom: 89px;
        margin-top: 157px;
    }
}

#contact .container {
    padding-left: 25px;
    padding-right: 25px;
}

.contact p {
    font-size: 22px;
    font-weight: 700;
    line-height: 1.6818181818;
    letter-spacing: 0.03em;
    @include mq('lg') {
        font-size: 30px;
    }
}

.contact__inner br {
    @include mq('lg') {
        display: none;
    }
}

.contact__btn {
    margin-top: 29px;
    text-align: center;
    @include mq('lg') {
        display: flex;
        justify-content: center;
        margin-top: 45px;
    }
}

// #contact 
#contact .common-btn {
    display: block;
    margin-inline: auto;
    width: 195px;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    padding: 13px 16px;

    @include mq('md') {
        border-radius: 50px;
        letter-spacing: 0.05em;
    }

    @include mq('lg') {
        width: 270px;
        font-size: 30px;
        font-weight: 600;
        margin-left: 21px;
        margin-right: 0;
    }
}

#contact .common-btn:not(:first-child) {
    margin-top: 21px;
    @include mq('lg') {
        margin-top: rem(0);
        margin-left: rem(41);
    }
}

