        // width: 550px;
        padding-top: 30px;
    }
    
    @include mq('xl') {
        border-radius: 20px;
        overflow: hidden;
        margin-right: 0;
        margin-left: rem(0);
        padding-top: rem(0);
        margin-top: 66px;
        width: 540px;
        max-width: 100%;
        position: relative;
        z-index: $layer-up;
        aspect-ratio: 541 / 356;
    }
}

