.screening__btn {
    padding-top: rem(52);
    text-align: center;
    margin-inline: auto;

    @include mq('md') {
        margin-top: 30px;
        display: flex;
        justify-content: center;
        padding-top: 0;
        // padding-top: rem(139);
        // padding-bottom: rem(177);
    }

    @include mq('xl') {
