    }
}

#screening .common-btn {
    padding: rem(11) rem(16);
    width: 278px;
    max-width: 100%;
    display: block;
    font-size: 20px;
    font-weight: 600;
    margin-inline: auto;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
    
    @include mq('md') {
        letter-spacing: 0.05em;
        width: 461px;
        max-width: 100%;
        // padding: 21px;
