.screening__below {
    @include mq('md') {
        position: relative;
        padding-bottom: 100px;

        &::before {
            content: '';
            position: absolute;
            background: url(../images/screening/sp_gradation_bg.png)no-repeat top center / cover;
            width: 100%;
            height: 100%;
            margin: 0 calc(50% - 50vw);
            width: 100vw;
        }
    }

    @include mq('lg') {
        padding-bottom: 0;

        &::before {
            display: none;
        }
    }

}