@use "global" as *;

.section-title {
    text-align: center;
    line-height: 1;
}

.section-title h2 {
    font-size: 24px;
    font-weight: 400;
    font-family: $second-font-family;
    position: relative;
    padding-bottom: rem(36);
    letter-spacing: 0.05em;
    line-height: 1.66666666;

    @include mq('md') {
        padding-bottom: rem(50);
        font-size: 28px;
        line-height: 1.5;
    }

    @include mq('lg') {
        font-size: 38px;
    }

    &::after {
        content: '';
        position: absolute;
        width: rem(60);
        height: rem(6);
        background: $main-color;
        opacity: 0.2;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);

        @include mq('md') {
            width: rem(100);
            height: rem(9);
        }
    }
}

.section-title span {
    display: inline-block;
    text-align: center;
    padding-bottom: rem(21);
    font-size: 30px;
    font-family: $english-font-family;
    font-weight: 700;
    color: $main-color;
    padding-left: rem(20);
    position: relative;
    opacity: 0.5;

    @include mq('md') {
        padding-bottom: rem(30);
        font-size: 45px;
    }
}


#screening .section-title span {
    @include mq('md') {
        padding-bottom: rem(37);
    }
}

.section-title--s span {
    &::before {
        content: 'S';
        position: absolute;
        font-size: 65px;
        font-weight: 700;
        opacity: 0.3;
        bottom: 24px;
        left: -11px;

        @include mq('md') {
            bottom: 43px;
            font-size: 96px;
            left: -41px;
            bottom: 17px;
        }
        @include mq('xl') {
            left: -30px;
            bottom: 45px;
        }
    }
}

.section-title--e span {
    &::before {
        content: 'E';
        position: absolute;
        font-size: 65px;
        font-weight: 700;
        opacity: 0.3;
        bottom: 24px;
        left: -33px;
        @include mq('md') {
            font-size: 96px;
            left: -45px;
            bottom: 12px;
        }
        @include mq('xl') {
            bottom: 41px;
            
        }
    }
}